import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
    const { data: mcbUtils } = useWPOptionQuery('mcbUtils');
    const { data = null, refetch, isLoading = true } = useWPAjax('mcbPipeChecker', { _wpnonce: mcbUtils?.nonce }, true);
    const isPremium = (!isLoading && data?.isPipe) || false;

    useEffect(() => {
        refetch()
    }, [mcbUtils]);

    return { isPremium, isLoading };
};
export default usePremiumInEditor;