

import { useState, useEffect } from 'react';
import { __ } from '@wordpress/i18n';

const { PanelBody, Button } = wp.components;
const { registerPlugin } = wp.plugins;
const { PluginSidebar, PluginSidebarMoreMenuItem } = wp.editPost;

import useWPOptionMutation from './hooks/useWPOptionMutation';
import useWPOptionQuery from './hooks/useWPOptionQuery';
import icons from './utils/icons';
import { generateString } from './utils/functions';
import mcb_prompt from './utils/mcbPrompt';

const mcbEvent = new CustomEvent('mcbEvent');

const RenderPlugin = () => {

    const [mcbData, setMcbData] = useState({});
    const { key = '', accessToken } = mcbData || {};

    const { data: firstData = {}, fetchData, isLoading: loadingOnGet } = useWPOptionQuery('mcb-email-collect');
    const { isLoading: loadingOnSet, saveData } = useWPOptionMutation('mcb-email-collect', { dataType: 'object' });


    // useEffect(() => {
    //     setMcbData({});
    //     saveData({});
    // }, []);

    // Get first data
    useEffect(() => {
        if (!loadingOnGet) {
            setMcbData({ ...firstData });
        }
    }, [loadingOnGet]);

    // Save to database
    // const onSaveData = () => {
    //     if (!loadingOnSet) {
    //         saveData(mcbData);
    //         // clearCache();
    //     }
    // }

    useEffect(() => {
        if (!loadingOnSet) {
            window.dispatchEvent(mcbEvent);
        }
    }, [loadingOnSet]);

    // Event Key 
    useEffect(() => {
        window.addEventListener('mcbEventOnEdit', () => {
            fetchData();
        });

    }, []);

    const unAuthorized = () => {
        setMcbData({ ...mcbData, accessToken: '' });
        saveData({ ...mcbData, accessToken: '' });
    }

    return <>
        <PluginSidebarMoreMenuItem target='mail-collections'>{__('MailChimp', 'mail-collections')}</PluginSidebarMoreMenuItem>

        <PluginSidebar className='bPlPluginSidebar' name='mcb-mailchimp' title={__('MailChimp', 'mail-collections')}>
            <PanelBody className='bPlPanelBody mcbPanelBody' title={__('Connection', 'mail-collections')} initialOpen={true}>
                {/* <div className="configHelp">
                    <InputControl className="mcbInputControl" value={key} onChange={val => { setMcbData({ ...mcbData, key: val }); }} />
                    <a href="https://bplugins.com/docs/mailchimp-block/api-key" target='_blank' rel='noreferrer'>Help</a>
                </div> */}
                {/* <Button className='apiBtn' disabled={loadingOnSet} onClick={onSaveData} >{__('Save Information', 'mail-collections')}</Button> */}

                <div className='mcbAuthorization mt15'>
                    {accessToken ? <Button onClick={unAuthorized} className="mcbAuthBtn ">{__('Sign Out', 'mail-collections')}</Button> : <Button className='mcbAuthBtn' onClick={async () => {
                        const state = generateString(15);
                        const url = `https://api.bplugins.com/mailchimp-auth/?state=${state}`

                        mcb_prompt(url, 670, 520, function () {
                            fetch(`${mcbAccessToken?.ajaxUrl}?action=mcb_get_access_token&state=${state}&nonce=${mcbAccessToken?.nonce}`).then(res => res.json()).then((data) => {

                                const { access_token } = data;
                                setMcbData({ ...mcbData, key: '', accessToken: access_token });
                                saveData({ ...mcbData, key: '', accessToken: access_token });
                            })
                        });
                    }} >  {__('Sign in with Mailchimp', 'mail-collections')}</Button>}
                </div>

            </PanelBody>
        </PluginSidebar>
    </>
};

registerPlugin('mcb-mailchimp', {
    icon: icons.mailchimp,
    render: RenderPlugin
});