

import { __ } from '@wordpress/i18n';
import { useState, useEffect } from '@wordpress/element';
import { RichText } from '@wordpress/block-editor';
import { produce } from 'immer';
import Settings from './Settings';
import Style from './Style';
import { tabController } from './utils/functions';
import useWPOptionQuery from './hooks/useWPOptionQuery';
import useWPOptionMutation from './hooks/useWPOptionMutation';
import Forms from './Forms';
import icons, { rightIcon } from './utils/icons';
import { SelectControl } from '@wordpress/components';
// import BPLSDK from '../bplugins_sdk/src/components/v1/BPLSDK';

const mcbEvent = new CustomEvent('mcbEventOnEdit');

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { elements, content, config, theme, layout } = attributes;
	const { placement } = layout;
	const { apiKey, audienceId } = config;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const [activeIndex, setActiveIndex] = useState(0);
	const [audienceList, setAudienceList] = useState([]);
	const [mcbData, setMcbData] = useState({});

	const { isLoading: loadingOnSet, saveData } = useWPOptionMutation('mcb-email-collect', { dataType: 'object' });
	const { data = {}, fetchData, isLoading } = useWPOptionQuery('mcb-email-collect');
	const { key, accessToken } = data || {};

	// get Audience Id 
	const getAudienceId = async () => {
		try {
			await fetch(`${mcbAudienceId?.ajaxUrl}?action=mcbSubmit_Form_AudienceId&nonce=${mcbAudienceId?.nonce}&apiKey=${key || apiKey}`).then(res => res.json()).then(data => {
				const list = JSON.parse(data).lists;
				setAudienceList(list);
			});
		} catch (error) {
			console.error('Error:', error);
		}
	}

	const getAudienceList = async () => {

		try {
			await fetch(`${mcbAudienceList?.ajaxUrl}?action=mcbAudienceList&nonce=${mcbAudienceList?.nonce}&accessToken=${accessToken}`).then(res => res.json()).then(data => {
				const endpoint_url = data.data.endpoint_url;
				const { lists } = data.data;
				setAttributes({ config: { ...config, endpoint_url: endpoint_url } });
				setAudienceList(lists);
			});
		} catch (error) {
			console.error('Error:', error);
		}
	}

	// Event Key 
	useEffect(() => {
		window.addEventListener('mcbEvent', () => {
			fetchData();
		});
		if (!isLoading) {
			setMcbData(data);
		}

	}, [isLoading, key]);

	useEffect(() => {
		if (!loadingOnSet) {
			window.dispatchEvent(mcbEvent);
		}
	}, [loadingOnSet]);

	useEffect(() => {
		if (!loadingOnSet) {
			fetchData();
		}
	}, [loadingOnSet]);

	useEffect(() => {
		if (accessToken) {
			getAudienceList();
		} else if (key?.length || apiKey?.length) {
			getAudienceId();
		}
		else {
			setAudienceList([]);
		}
	}, [accessToken, key, apiKey]);

	// Loadding check 
	if (isLoading) {
		return <div className='mcbLoading'>{icons.preloader}</div>
	}

	// Save to database
	const onSaveData = () => {
		if (!loadingOnSet) {
			saveData(mcbData);
			// clearCache();
		}
	}

	const updateObject = (obj, key, val, otherKey = null) => {
		const newObj = produce(attributes[obj], draft => {
			if (null !== otherKey) {
				draft[key][otherKey] = val;
			} else {
				draft[key] = val;
			}
		});
		setAttributes({ [obj]: newObj });
	}

	if (!accessToken && !key) {
		return <div className='authorizationBtn'>
			<div className='buttonAuth'>
				<div className="content" onClick={() => {
					wp.data.dispatch('core/edit-post').openGeneralSidebar('mcb-mailchimp/mcb-mailchimp');
				}}>
					<div className="text">{__('Configure', 'mail-collections')}</div>
					<div className="icon">{rightIcon}</div>
				</div>
			</div>
		</div>
	}

	// Audience list 
	const audienceOpt = audienceList?.map((audience) => {
		return { label: audience.name, value: audience.id }
	});

	// check Audience Id 
	if (audienceId === '') {
		return <div className='mcbNotice'>
			<h2>{__('You must select an audience', 'mail-collections')}</h2>
			<SelectControl className='' label={__('Select Audience', 'mail-collections')} options={[{ label: 'Select', value: '' }, ...audienceOpt]} value={config?.audienceId} onChange={(val) => { setAttributes({ config: { ...config, audienceId: val } }); }} />
		</div>
	}

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} audienceList={audienceList} audienceOpt={audienceOpt} updateObject={updateObject} />
		{/* <BPLSDK setAttributes={setAttributes} /> */}

		{(placement === "auto-popup" || placement === "floating-bottom" || placement === "floating-top") && <div className='mcbNotice'><h2>{__('This preview is only visible in the frontend', 'mail-collections')}</h2></div>}

		{(placement === "normal" || placement === "modal") &&
			<div className={className} id={`mcbMailChimp-${clientId}`}>
				<Style attributes={attributes} clientId={clientId} />

				<Forms attributes={attributes} apiKey={key} accessToken={accessToken}>
					{elements?.title && <RichText tagName="h2" className='' value={content?.title} onChange={val => setAttributes({ content: { ...content, title: val } })} placeholder={__('Title Here', 'mail-collections')} inlineToolbar />}

					{((theme !== "form7" && placement !== "floating-top") && elements?.desc) && <RichText tagName="p" className='' value={content?.desc} onChange={val => setAttributes({ content: { ...content, desc: val } })} placeholder={__('Description Here', 'mail-collections')} inlineToolbar />}
				</Forms>
			</div>
		}
	</>;
};
export default Edit;