
import { useState } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { produce } from 'immer';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TabPanel, TextControl, ToggleControl, SelectControl, __experimentalBoxControl as BoxControl, __experimentalUnitControl as UnitControl, RangeControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';

// Settings Components
import { Label, Background, BColor, BorderControl, ColorsControl, Typography, HelpPanel, BtnGroup, BDevice, InlineDetailMediaUpload } from '../../Components';

import ProModal from '../../Pro-Model/ProModal';
import { BControlPro, SelectControlPro } from '../../Components/Pro';
import patterns from '../patterns.json';

import { tabController, checkTheme, checkLayout } from './utils/functions';
import options, { aligns, cacheTypeOpt, placementOpt, popupBtnOpt } from './utils/options';
import { PanelRow } from '@wordpress/components';
import { perUnit } from '../../Components/utils/options';
const { generalStyleTabs, pxUnit, emUnit } = options;

import { parse } from '@wordpress/blocks';

import usePremiumInEditor from './hooks/usePremiumInEditor';
import { correct, notice } from './utils/icons';

const Settings = ({ attributes, setAttributes, audienceOpt, updateObject, clientId }) => {
	const { config, elements, content, message, placeholder, theme, icon, iconBG, titleTypo, titleColor, descTypo, descColor, formBG, formPadding, formBorder, fieldTypo, fieldColors, fieldPadding, fieldBorder, fieldMargin, btnTypo, btnColors, btnHovColors, btnPadding, btnBorder, messageColors, messageTypo, themeWidth, themeAlign, popupBtn, form4LeftImage, brandLogo, inputLabel, layout, closeBtn, titleMargin, descMargin, btnMargin, modal, imgArea } = attributes;
	const { placement, popupTimeOut, cacheTimeType, cacheTime } = layout;
	const [themeWidthDevice, setThemeWidthDevice] = useState('desktop');
	const [proModalOpen, setProModalOpen] = useState(false);
	const { isPremium } = usePremiumInEditor();

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel mcbTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>

					<HelpPanel slug="block-for-mailchimp" docsLink="https://bplugins.com/docs/mailchimp-block" />

					<PanelBody className='bPlPanelBody mcbPanelBody' title={__('Config', 'mail-collections')} initialOpen={false}>
						<div className='mcbConfigApiKey'>
							<button className='mcbBtn' onClick={() => {
								wp.data.dispatch('core/edit-post').openGeneralSidebar('mcb-mailchimp/mcb-mailchimp');
							}} >{__('Connection', 'mail-collections')}</button>
						</div>
						<SelectControl className='' label={__('Select Audience', 'mail-collections')} options={[{ label: 'Select', value: '' }, ...audienceOpt]} value={config?.audienceId} onChange={(val) => { setAttributes({ config: { ...config, audienceId: val } }); }} />
					</PanelBody>

					<PanelBody className="bPlPanelBody" title={__('Elements', 'mail-collections')} initialOpen={false}>

						{('form1' === theme || 'form2' === theme) && <ToggleControl className='mt10' label={__('Icon', 'mail-collections')} checked={elements?.icon} onChange={(val) => updateObject('elements', 'icon', val)} />}

						<ToggleControl className='mt10' label={__('Title', 'mail-collections')} checked={elements?.title} onChange={(val) => updateObject('elements', 'title', val)} />

						{'form7' !== theme && <ToggleControl className='mt10' label={__('Description', 'mail-collections')} checked={elements?.desc} onChange={(val) => updateObject('elements', 'desc', val)} />}

						{('default' === theme || 'form3' === theme || 'form4' === theme || 'form5' === theme || 'form6' === theme || 'form9' === theme || 'form10' === theme) && <>
							<ToggleControl className='mt10' label={__('First Name', 'mail-collections')} checked={elements?.fName} onChange={(val) => updateObject('elements', 'fName', val)} />

							<ToggleControl className='mt10' label={__('Last Name', 'mail-collections')} checked={elements?.lName} onChange={(val) => updateObject('elements', 'lName', val)} />
						</>}

						{
							'form8' === theme && <ToggleControl className='mt10' label={__('Input Label', 'mail-collections')} checked={inputLabel?.label} onChange={(val) => updateObject('inputLabel', 'label', val)} />
						}

					</PanelBody>

					<PanelBody className="bPlPanelBody" title={__('Layout', 'mail-collections')} initialOpen={false}>

						<SelectControlPro labelPosition='left' className="bPlPanelBody mt10" label={__('Placement', 'mail-collections')} options={placementOpt} value={placement} onChange={val => {
							setAttributes({ layout: { ...layout, placement: val }, ...checkLayout(val, theme) })
						}} isPremium={isPremium} setOpen={setProModalOpen} proValues={['modal', 'auto-popup', 'floating-bottom', 'floating-top']} />

						{placement === "auto-popup" && <RangeControl className='mt10' label={__('Time', 'mail-collections')} value={popupTimeOut} onChange={(val) => updateObject('layout', 'popupTimeOut', val)} min={0} max={20} step={0.1} />}
					</PanelBody>

					{
						(placement === "auto-popup" || placement === "floating-top" || placement === "floating-bottom") && <PanelBody className="bPlPanelBody" title={__('Cache', 'mail-collections')} initialOpen={false}>
							<div className="cacheTime">
								<SelectControl labelPosition='left' className="mt10" label={__('Type', 'mail-collections')} value={cacheTimeType} options={cacheTypeOpt} onChange={val => {
									updateObject('layout', 'cacheTimeType', val)
									localStorage.setItem("modalClosedTime", '');
								}} />

								<NumberControl label={__('Time', 'mail-collections')} className='mt10' value={cacheTime} onChange={val => {
									updateObject('layout', 'cacheTime', parseInt(val))
									localStorage.setItem("modalClosedTime", '');
								}} />
							</div>
						</PanelBody>
					}


					{theme === 'form8' &&
						<PanelBody className='bPlPanelBody' title={__('Logo', 'mail-collections')} initialOpen={false}>

							<InlineDetailMediaUpload className='mb10' label={__('Upload', 'mail-collections')} value={brandLogo?.img} type={['image']} onChange={val => updateObject('brandLogo', 'img', val)} isPremium={isPremium} Component={InlineDetailMediaUpload} setOpen={setProModalOpen} />

							<BControlPro className='mt10' label={__('Size', 'mail-collections')} value={brandLogo?.size} onChange={val => updateObject('brandLogo', 'size', val)} min={2} max={500} isPremium={isPremium} Component={RangeControl} setOpen={setProModalOpen} />
						</PanelBody>}

					<PanelBody className='bPlPanelBody' title={__('Button', 'mail-collections')} initialOpen={false}>
						<TextControl label='' value={content.button} onChange={(val) => setAttributes({ content: { ...content, button: val } })} />
					</PanelBody>

					{
						placement === "modal" && <PanelBody className='bPlPanelBody' title={__('Popup Button', 'mail-collections')} initialOpen={false}>
							<TextControl label={__('Text', 'mail-collections')} className='' value={popupBtn?.text} onChange={val => updateObject("popupBtn", "text", val)} />
						</PanelBody>
					}

					<PanelBody className='bPlPanelBody' title={__('Message', 'mail-collections')} initialOpen={false}>
						<Label className='mt5'>{__('Success', 'mail-collections')}</Label>
						<TextControl label='' value={message.success} onChange={(val) => { setAttributes({ message: { ...message, success: val } }) }} />

						<Label className='mt5'>{__('Error', 'mail-collections')}</Label>
						<TextControl label='' value={message?.error} onChange={(val) => { setAttributes({ message: { ...message, error: val } }) }} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Placeholder Text', 'mail-collections')} initialOpen={false}>
						{('default' === theme || 'form3' === theme) && <>

							{elements?.fName && <>
								<Label className='mt5'>{__('First Name', 'mail-collections')}</Label>
								<TextControl label='' value={placeholder?.fName} onChange={(val) => { setAttributes({ placeholder: { ...placeholder, fName: val } }) }} /> </>}

							{elements?.lName && <>
								<Label className='mt5'>{__('Last Name', 'mail-collections')}</Label>
								<TextControl label='' value={placeholder?.lName} onChange={(val) => { setAttributes({ placeholder: { ...placeholder, lName: val } }) }} /></>
							}
						</>}
						<Label className='mt5'>{__('Email', 'mail-collections')}</Label>
						<TextControl value={placeholder?.email} onChange={(val) => { setAttributes({ placeholder: { ...placeholder, email: val } }) }} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Themes', 'mail-collections')} initialOpen={false}>

						{
							placement !== 'floating-top' && <><SelectControlPro label={__('Select Theme', 'mail-collections')} labelPosition="side" value={theme} options={options?.forms} onChange={(val) => { setAttributes({ theme: val, ...checkTheme(val), ...checkLayout(layout?.placement, val) }) }} isPremium={isPremium} setOpen={setProModalOpen} proValues={['form4', 'form5', 'form6', 'form7', 'form8', 'form9', 'form10']} />
								<div className="bpNotice"> {notice} Some settings/options will be changed while changing the theme.</div>
							</>
						}

						{
							placement === "normal" && <>

								{/* Width define option  */}
								<PanelRow className='m0'>
									<Label mt='0'>{__('Width:', 'mail-collections')}</Label>
									<BDevice device={themeWidthDevice} onChange={val => setThemeWidthDevice(val)} />
								</PanelRow>

								<UnitControl value={themeWidth[themeWidthDevice]} onChange={val => { setAttributes({ themeWidth: { ...themeWidth, [themeWidthDevice]: val } }) }} units={[pxUnit(550), perUnit(100)]} isResetValueOnUnitChange={true} />
								<PanelRow className='m0'>
									<p></p>
									<BDevice device={themeWidthDevice} onChange={val => setThemeWidthDevice(val)} />
								</PanelRow>
								<PanelRow className='themeAlign m0'>
									<Label className="m0" mt='0'>{__('Align:', 'mail-collections')}</Label>
									<BtnGroup value={themeAlign[themeWidthDevice]} onChange={val => setAttributes({ themeAlign: { ...themeAlign, [themeWidthDevice]: val } })} options={aligns} isIcon={true} />
								</PanelRow>
							</>
						}

					</PanelBody>
				</>}

				{'style' === tab.name && <>

					<PanelBody className='bPlPanelBody' title={__('Form', 'mail-collections')} initialOpen={false}>
						<Background className='mb10' label={__('Background:', 'mail-collections')} value={formBG} onChange={val => setAttributes({ formBG: val })} />

						<BoxControl label={__('Padding', 'mail-collections')} values={formPadding} onChange={val => setAttributes({ formPadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />

						<BorderControl className='' label={__('Border', 'mail-collections')} value={formBorder}
							onChange={(val) => { setAttributes({ formBorder: val }) }} />

					</PanelBody>

					{
						(theme === 'form4' || theme === 'form5' || theme === 'form6' || theme === 'form8' || theme === 'form9' || theme === 'form10') && <PanelBody className='bPlPanelBody' title={__('Info Area', 'mail-collections')} initialOpen={false}>

							<Background className='mb10' label={__('Background:', 'mail-collections')} value={form4LeftImage} onChange={val => setAttributes({ form4LeftImage: val })} isPremium={isPremium} Component={Background} setOpen={setProModalOpen} />

						</PanelBody>
					}

					{(elements?.icon && ('form1' === theme || 'form2' === theme)) &&
						<PanelBody className='bPlPanelBody' title={__('Icon', 'mail-collections')} initialOpen={false}>
							<Background className='mb10' label={__('Background', 'mail-collections')} value={iconBG} onChange={val => setAttributes({ iconBG: val })} />

							<BColor className='mb10' label={__('Color', 'mail-collections')} value={icon?.color} onChange={val => setAttributes({ icon: { ...icon, color: val } })} />
						</PanelBody>}

					{elements?.title &&
						<PanelBody className='bPlPanelBody' title={__('Label', 'mail-collections')} initialOpen={false}>
							<Typography className='mt10' label={__('Typography', 'mail-collections')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} produce={produce} />

							<BColor className="mb10" label={__('Color', 'github')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} />

							<BControlPro label={__('Margin', 'mail-collections')} values={titleMargin} onChange={val => setAttributes({ titleMargin: val })} resetValues={{ top: 0, right: 0, bottom: '15px', left: 0 }} units={[pxUnit(3), emUnit(2)]} Component={BoxControl} isPremium={isPremium} setOpen={setProModalOpen} />
						</PanelBody>}

					{elements?.desc &&
						<PanelBody className='bPlPanelBody' title={__('Description', 'mail-collections')} initialOpen={false}>
							<Typography className='mt10' label={__('Typography', 'mail-collections')} value={descTypo} onChange={val => setAttributes({ descTypo: val })} produce={produce} />

							<BColor className="mb10" label={__('Color', 'github')} value={descColor} onChange={val => setAttributes({ descColor: val })} />

							<BControlPro label={__('Margin', 'mail-collections')} values={descMargin} onChange={val => setAttributes({ descMargin: val })} resetValues={{ top: 0, right: 0, bottom: "10px", left: 0 }} units={[pxUnit(3), emUnit(2)]} Component={BoxControl} isPremium={isPremium} setOpen={setProModalOpen} />
						</PanelBody>}

					{
						theme === 'form5' &&
						<PanelBody className='bPlPanelBody' title={__('Image', 'mail-collections')} initialOpen={false}>

							<PanelRow className='m0'>
								<Label mt='0'>{__('Height:', 'mail-collections')}</Label>
								<BDevice device={themeWidthDevice} onChange={val => setThemeWidthDevice(val)} />
							</PanelRow>

							<UnitControl value={imgArea?.height[themeWidthDevice]} onChange={val => {
								setAttributes({ imgArea: { ...imgArea, height: { ...imgArea?.height, [themeWidthDevice]: val } } })
							}} units={[pxUnit(200), perUnit(20)]} isResetValueOnUnitChange={true} />
						</PanelBody>
					}

					<PanelBody className='bPlPanelBody' title={__('Input', 'mail-collections')} initialOpen={false}>

						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={fieldTypo} onChange={val => setAttributes({ fieldTypo: val })} produce={produce} />

						<ColorsControl className='mb15' label={__('Colors', 'mail-collections')} value={fieldColors} onChange={val => setAttributes({ fieldColors: val })} defaults={{ color: '#fff', bg: '#000' }} />

						<BoxControl label={__('Padding', 'mail-collections')} values={fieldPadding} onChange={val => setAttributes({ fieldPadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />

						<BorderControl className='mb15' label={__('Border', 'mail-collections')} value={fieldBorder}
							onChange={(val) => { setAttributes({ fieldBorder: val }) }} />

						<BoxControl label={__('Margin', 'mail-collections')} values={fieldMargin} onChange={val => setAttributes({ fieldMargin: val })} resetValues={{ top: 0, right: 0, bottom: '10px', left: 0 }} units={[pxUnit(3), emUnit(2)]} />
					</PanelBody>

					{theme === "form8" && <PanelBody className='bPlPanelBody' title={__('Input Label', 'mail-collections')} initialOpen={false}>

						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={inputLabel?.typo} onChange={val => updateObject('inputLabel', 'typo', val)} produce={produce} />

						<BColor className="mt10" label={__('Color', 'mail-collections')} value={inputLabel?.color} onChange={val => updateObject('inputLabel', 'color', val)} />
					</PanelBody>}

					<PanelBody className='bPlPanelBody' title={__('Button', 'mail-collections')} initialOpen={false}>
						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={btnTypo} onChange={val => setAttributes({ btnTypo: val })} produce={produce} />

						<ColorsControl className='' label={__('Colors', 'mail-collections')} value={btnColors} onChange={val => setAttributes({ btnColors: val })} />

						<ColorsControl className='mb10' label={__('Hovers Colors', 'mail-collections')} value={btnHovColors} onChange={val => setAttributes({ btnHovColors: val })} />

						<BoxControl label={__('Padding', 'mail-collections')} values={btnPadding} onChange={val => setAttributes({ btnPadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />

						<BorderControl className='mt10 mb10' label={__('Border', 'mail-collections')} value={btnBorder}
							onChange={(val) => { setAttributes({ btnBorder: val }) }} />

						<BControlPro className="mb10" label={__('Margin', 'mail-collections')} values={btnMargin} onChange={val => setAttributes({ btnMargin: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} Component={BoxControl} isPremium={isPremium} setOpen={setProModalOpen} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Message', 'mail-collections')} initialOpen={false}>

						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={messageTypo} onChange={val => setAttributes({ messageTypo: val })} produce={produce} />

						<BColor label={__('Success', 'mail-collections')} value={messageColors?.success} onChange={(val) => { setAttributes({ messageColors: { ...messageColors, success: val } }) }} />

						<BColor label={__('Error', 'mail-collections')} value={messageColors?.error} onChange={(val) => { setAttributes({ messageColors: { ...messageColors, error: val } }) }} />

					</PanelBody>

					{
						(placement === "modal" || placement === "auto-popup" || placement === "floating-bottom") &&
						<PanelBody className='bPlPanelBody' title={__('Modal', 'mail-collections')} initialOpen={false}>

							<PanelRow className='m0'>
								<Label mt='0'>{__('Width:', 'mail-collections')}</Label>
								<BDevice device={themeWidthDevice} onChange={val => setThemeWidthDevice(val)} />
							</PanelRow>

							<UnitControl value={modal?.width[themeWidthDevice]} onChange={val => {
								setAttributes({ modal: { ...modal, width: { ...modal?.width, [themeWidthDevice]: val } } })
							}} units={[pxUnit(400), perUnit(21)]} isResetValueOnUnitChange={true} />
						</PanelBody>
					}

					{placement === "modal" && <PanelBody className='bPlPanelBody' title={__('Popup Button', 'mail-collections')} initialOpen={false}>

						<SelectControl labelPosition='left' className='mb10' label={__('Align', 'mail-collections')} options={popupBtnOpt} value={popupBtn?.align} onChange={val => updateObject('popupBtn', 'align', val)} />

						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={popupBtn?.typo} onChange={val => updateObject("popupBtn", "typo", val)} produce={produce} />

						<ColorsControl className='mb15' label={__('Colors', 'mail-collections')} value={popupBtn?.colors} onChange={val => updateObject("popupBtn", "colors", val)} defaults={{ color: '#000', bg: '#fff' }} produce={produce} />

						<ColorsControl className='mb15' label={__('Hover Colors', 'mail-collections')} value={popupBtn?.hoverColors} onChange={val => updateObject("popupBtn", "hoverColors", val)} defaults={{ color: '#fff', bg: '#000' }} produce={produce} />

						<BoxControl label={__('Padding', 'mail-collections')} values={popupBtn?.padding} onChange={val => updateObject("popupBtn", "padding", val)} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} produce={produce} />

						<BorderControl className='' label={__('Border', 'mail-collections')} value={popupBtn?.border}
							onChange={(val) => updateObject("popupBtn", "border", val)} produce={produce} />
					</PanelBody>}

					{placement !== "normal" && <PanelBody className='bPlPanelBody' title={__('Close Icon', 'mail-collections')} initialOpen={false}>
						<RangeControl className='mt5' label={__('Size', 'mail-collections')} value={closeBtn?.size}
							onChange={(val) => updateObject('closeBtn', 'size', val)} />

						<ColorsControl className='mt10' label={__('Colors', 'mail-collections')} value={closeBtn?.colors}
							onChange={val => updateObject('closeBtn', 'colors', val)} defaults={{ color: '#fff', bg: '#000' }} />
					</PanelBody>}
				</>}

				{"patterns" === tab.name && <>

					{!isPremium && <p className='noticePatter'>{__('it\'s only available on pro version', 'slider')}</p>}
					{patterns.map((pattern, index) => {
						const imgName = pattern.name.replace('/', '-');
						const imgPath = mcbInfo.patternsImagePath + imgName + '.png';
						return <div key={index} className='mcbPatters'>
							<img key={index} style={{ width: '100%' }} src={imgPath} />
							{/* <BlockPreview blocks={parse(pattern.content)} viewportWidth={700} /> */}
							<div className="overlyButton">
								<button onClick={() => {
									if (isPremium) {
										const blocks = parse(pattern.content);
										wp.data.dispatch('core/block-editor').replaceBlock(clientId, blocks);
									} else {
										setProModalOpen(true);
									}
								}}>{__('Apply', 'slider')}</button>
							</div>
						</div>
					})}
				</>}
			</>}</TabPanel>
		</InspectorControls>

		<ProModal isProModal={proModalOpen} setIsProModal={setProModalOpen} pluginName="Mailchimp Block" priceLink="https://bplugins.com/products/mailchimp-block/#pricing">
			<li className="item">{correct}{__('Four layout options: Normal, Modal, Auto Popup, Floating Top, and Floating Bottom.', 'mail-collection')}</li>
			<li className="item">{correct}{__('Ten distinct themes: Default, Theme 1, Theme 2, Theme 3, Theme 4, Theme 5, Theme 6, Theme 7, Theme 8, and Theme 9.', 'mail-collection')}</li>
			<li className="item">{correct}{__('Customizable margins for labels, descriptions, and buttons.', 'mail-collection')}</li>
			<li className="item">{correct}{__('Added shortcode support for easy embedding.', 'mail-collection')}</li>
			<li className="item">{correct}{__('Multiple pattern configurations for a tailored look.', 'mail-collection')}</li>
			<li className="item">{correct}{__('Configurable auto-popup layout duration.', 'mail-collection')}</li>
			<li className="item">{correct}{__('Supports logo uploads for seamless branding.', 'mail-collection')}</li>
		</ProModal>
	</>;
};
export default Settings;